<template>
  <el-container id="sideH" :style="containerStyle">
    <el-aside style="width: 300px"  class="leftBg"  v-show="!menuCollapse">
      <div class="logo" style="border:none;padding: 0;">
        <!--<img style="margin: 8px auto; padding: 0;width:190px;height:54px;" src="../../../assets/logo_1.png">-->
      </div>
      <NavMenu></NavMenu>
    </el-aside>
    <el-container>
      <el-header>
        <div class="user">
          <span  @click="toggleMenu"> <i class="iconfont icon-showleft" ></i></span>
          <span class="userBLock">
            <el-tooltip class="item" effect="dark" content="个车" placement="bottom-start">
              <i class="iconfont icon-iconfontzhizuobiaozhun023105"></i>
             </el-tooltip>
             <el-tooltip class="item" effect="dark" content="车队" placement="bottom-start">
              <i class="iconfont icon-cheliangleixing"></i>
             </el-tooltip>
             <el-tooltip class="item" effect="dark" content="审批" placement="bottom-start">
              <i class="iconfont icon-shengpi"></i>
             </el-tooltip>
             <el-tooltip class="item" effect="dark" content="预算" placement="bottom-start">
              <i class="iconfont icon-yusuan"></i>
             </el-tooltip>
             <el-tooltip class="item" effect="dark" content="车险" placement="bottom-start">
              <i class="iconfont icon-cheliangbaoxianjilu"></i>
             </el-tooltip>
          </span>
          <el-button type="text" icon="iconfont icon-tuichu2" class="option" @click="logout"></el-button>
        </div>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import {mapState} from 'vuex'
import NavMenu from './NavMenu'

export default {
  name: 'Main',
  components: {NavMenu},
  data () {
    return {
      clientHeight: ''
    }
  },
  mounted () {
    this.clientHeight = `${document.documentElement.clientHeight}px`
    window.onresize = () => {
      this.clientHeight = `${document.documentElement.clientHeight}px`
    }
  },
  computed: {
    ...mapState({
      menuCollapse: state => state.app.menuCollapse
    }),
    containerStyle () {
      return {height: this.clientHeight}
    }
  },
  methods: {
    toggleMenu () {
      this.$store.commit('TOGGLE_MENU_COLLAPSE')
    },
    logout () {
      this.$confirm('确认要退出登录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$store.dispatch('LogOut').then(() => {
          this.$router.push({path: '/'})
        })
      })
    }
  }
}
</script>

<style scoped>
  .leftBg{
    background: url('../../assets/img/leftBg.png') no-repeat center bottom;
    height: 218px;
    width: 310px;
    z-index: 2;
    box-shadow:#DCE7FA 4px -3px 14px;
  }
  .el-aside {
    height: 100%;
    overflow: auto;
    color: #333;
  }

  .el-aside > .logo {
    height: 60px;
    line-height: 60px;
    font-size: 25px;
    text-align: center;
    background-color: rgb(255, 255, 255);
    border-right: 1px solid #eee;
    border-bottom: 1px solid #eee;
  }

  .el-header {
    /*background-color: #B3C0D1;*/
    background-color: #fff;
    color: #333;
    line-height: 60px;
  }

  .el-main {
    background-color: rgba(247, 249, 255, 1);
  }

  .el-menu {
    list-style: none;
    position: relative;
    margin: 0;
    padding-left: 0;
    border-right: none;
  }
  .el-menu >>> .el-menu-item,
  .el-menu >>> span{
    font-size: 15px;
  }

  .el-menu >>> .el-icon-menu{
    color: #1ea8f0
  }

  .user .option {
    float:right;
    position: relative;
    top:11px;
    right: 20px;
    margin: 0;
  }

</style>



