<template>
  <el-menu router unique-opened :collapse="menuCollapse">
    <menu-item :menus="menus"></menu-item>
  </el-menu>
</template>

<script>
import {mapState} from 'vuex'
const MenuItem = () => import('./MenuItem')

export default {
  name: 'NavMenu',
  components: {MenuItem},
  data () {
    return {
    }
  },
  computed: mapState({
    menuCollapse: state => state.app.menuCollapse,
    menus: state => state.user.menus
  })
}
</script>

<style scoped>
</style>



